  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding-right: 0 !important;
  z-index: 13050;
  -webkit-overflow-scrolling: touch;
  outline: 0; }
  .modal.fade .modal-dialog {
    transition: transform 0.2s ease-in;
    top: 14%;
    left: 50%;
    transform: translate(-50%, 0);
    opacity: 0;
    transition: opacity .15s linear; }
    @media (max-width: 767px) {
      .modal.fade .modal-dialog {
        top: 69px; } }
  .modal.in .modal-dialog {
    transition: transform 0.2s ease-in;
    opacity: 1; }

.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto; }

.fade {
  opacity: 0;
  transition: opacity .15s linear; }
  .fade.in {
    opacity: 1; }

.modal-dialog {
  position: absolute;
  width: 90%;
  margin: 0 0 30px;
  border: 7px solid #26c6da;
  border-radius: 30px 0 30px 30px;
  overflow: hidden; }

.modal-content {
  font-size: 24px;
  color: #333;
  font-family: 'Roboto-Regular', Arial, Helvetica, sans-serif;
  position: relative;
  background-color: #fff;
  border: 0 solid transparent;
  border-radius: 0;
  background-clip: padding-box;
  outline: 0;
  transform: translate3d(0, 0, 0);
  padding: 35px 59px 21px 57px;
  text-align: center; }
  @media (max-width: 767px) {
    .modal-content {
      padding: 40px 20px 30px;
      font-size: 20px; } }

.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  background-color: rgba(0, 0, 0, 0.73); }
  .modal-backdrop.fade {
    opacity: 0; }
  .modal-backdrop.in {
    opacity: 1; }

.modal-dialog .close {
  transition: color 0.3s ease-in-out;
  position: absolute;
  top: 17px;
  right: 2px;
  background: none;
  border: none;
  color: #26c6da;
  font-size: 63px;
  padding: 5px;
  line-height: 15px;
  outline: none; }
  @media (max-width: 767px) {
    .modal-dialog .close {
      top: 10px;
      right: 10px;
      font-size: 45px; } }
  .modal-dialog .close:hover {
    color: #24b9cc; }

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll; }

.modal-title {
  font-family: 'Roboto-Bold', Arial, Helvetica, sans-serif;
  font-size: 36px;
  color: #26c6da;
  text-transform: uppercase;
  text-align: center;
  display: block;
  margin: 0 0 26px; }
  @media (max-width: 767px) {
    .modal-title {
      font-size: 25px; } }

.order-form {
  margin: 47px 0 0; }
  .order-form .simple-input {
    display: block;
    width: 100%;
    max-width: 415px;
    margin: 0 auto 26px;
    text-align: center; }
  .order-form .btn-simple {
    display: block;
    width: 100%;
