@media (min-width: 768px) {
  .modal-dialog {
    width: 700px;
    margin: 45px auto; }
  .modal-sm {
    width: 300px; } }

@media (min-width: 1024px) {
  .modal-dialog {
    width: 700px; }
  .modal-lg {
    width: 700px; } }

.burger-btn {
  transition: margin 0.3s ease-in-out;
  float: right;
  display: none;
  position: relative;
  width: 55px;
  height: 55px;
  border: none;
  overflow: hidden;
  padding: 14px 16px;
  z-index: 5;
  font-size: 0;
  outline: none;
  background: none; }
  @media (max-width: 979px) {
    .burger-btn {
      display: block;
      margin: 0 0 0 29px;
      width: 42px;
      height: 42px; } }
  @media (max-width: 767px) {
    .burger-btn {
      width: 35px;
      height: 35px;
      margin: 0 17px 0 15px; } }
  .burger-btn span {
    transition: transform 0.2s ease-in-out, margin 0.2s ease-in-out;
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    width: 20px;
    height: 2px;
    margin-left: -10px;
    margin-top: 0;
    background-color: #fff;
    user-select: none; }
    @media (max-width: 979px) {
      .burger-btn span {
        width: 25px;
        margin-left: -7px; } }
    @media (max-width: 767px) {
      .burger-btn span {
        width: 15px;
        margin-left: -7px; } }
    .burger-btn span:before, .burger-btn span:after {
      transition: transform 0.2s ease-in-out, margin 0.2s ease-in-out;
      content: '';
      position: absolute;
      left: 0;
      border-radius: 1px;
      width: 100%;
      height: 100%;
      background: #fff;
      margin-top: -6px; }
    .burger-btn span:after {
      margin-top: 6px; }
  .burger-btn.active span {
    background-color: transparent; }
    .burger-btn.active span:before {
      transform: rotate(45deg);
      margin-top: 0; }
    .burger-btn.active span:after {
      transform: rotate(-45deg);
      margin-top: 0; }

